@import 'styles/common/vars';
.form {
  margin-bottom: 80px;
  @media screen and (min-width: 768px) {
    max-height: 320px;
    display: flex;
    justify-content: space-between;
  }
  @media screen and (min-width: 1440px) {
    max-height: none;
    width: 833px;
  }

  &__wrapper {
    position: relative;
    height: 320px;
    margin-bottom: 40px;
    border-radius: 8px;
    color: rgba(22, 31, 55, 0.5);
    background: rgba(22, 31, 55, 0.5);
    @media screen and (min-width: 768px) {
      width: 320px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 1440px) {
      width: 400px;
      height: 400px;
    }
  }

  &__input {
    font-family: 'Manrope', sans-serif;
    font-size: 14px;
    display: block;
    width: 100%;
    margin-bottom: 34px;
    color: $primary-white-color;
    letter-spacing: -0.28px;
    background-color: inherit;
    padding-bottom: 14px;
    border: none;
    letter-spacing: -0.28px;
    border-bottom: 1px solid rgba(243, 243, 243, 0.7);
    @media screen and (min-width: 375px) {
      width: 335px;
    }
    @media screen and (min-width: 768px) {
      width: 352px;
      margin-bottom: 40px;
      padding-bottom: 18px;
      font-size: 16px;
    }
    @media screen and (min-width: 1440px) {
      width: 393px;
    }

    &__::placeholder {
      opacity: 0.5;
    }
  }
}

.file {
  padding-top: 116px;
  @media screen and (min-width: 1440px) {
    padding-top: 156px;
  }

  &__input {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    width: 50px;
    height: 50px;
    margin: auto 0;
    cursor: pointer;
    opacity: 0;
    @media screen and (min-width: 768px) {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
  }

  &__btn {
    position: absolute;
    top: 110px;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    width: 50px;
    height: 50px;
    pointer-events: none;
    z-index: 2;
    border-radius: 6px;

    @media screen and (min-width: 1440px) {
      top: 150px;
    }
  }

  &__text {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 122px;
    font-size: 16px;
    font-weight: 500;
    line-height: calc(20 / 16);
    color: $primary-white-color;

    @media screen and (min-width: 1440px) {
      bottom: 156px;
    }
  }
}

.img {
  width: 335px;
  height: 320px;
  border-radius: 8px;
  @media screen and (min-width: 768px) {
    width: 320px;
  }
  @media screen and (min-width: 1440px) {
    width: 400px;
    height: 400px;
  }

  &__clear {
    position: absolute;
    right: -10px;
    top: -10px;
    background-color: $disabled-blue-color;
    padding: 2px 2px;
    border: none;
    border-radius: 50%;
    transition: background-color $transition-duration;

    line-height: 0.5;

    &:hover,
    &:focus {
      background-color: $secondary-bg-blue-color;
    }
  }
}

.error {
  position: absolute;
  color: $error-red-color;
  font-size: 12px;

  &__drink {
    top: 34px;
    @media screen and (min-width: 768px) {
      top: 43px;
    }
  }

  &__description {
    top: 105px;
    @media screen and (min-width: 768px) {
      top: 125px;
    }
  }

  &__category {
    bottom: 54px;
    @media screen and (min-width: 768px) {
      bottom: 103px;
    }
    @media screen and (min-width: 1440px) {
      bottom: 182px;
    }
  }

  &__img {
    bottom: 90px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    @media screen and (min-width: 768px) {
      bottom: 105px;
      width: 170px;
    }
    @media screen and (min-width: 1440px) {
      bottom: 140px;
    }
  }
}

.category {
  position: relative;
}
